<template>
    <div class="viewpage">
        <div class="sec-header">
            <img src="https://images.bthhotels.com/image/recruitImg/recruitManager/recListBanner.png" />
        </div>
        <div class="sec-nav">
            <div class="cur-category">
                <span class="iconfont icon-local"></span>
                {{firTitle}}
            </div>
            <ul>
                <li :class="['item', {'item-active': index == currentIndex}]" 
                    v-for="(item, index) in getSecondNav" 
                    :key="index" @click="selectTag(item)">
                    {{item.title}}
                </li>
            </ul>
        </div>
        <div class="content-wrapper">
            <transition name='secrouter'>
                <router-view ref="secView" :key="activeDate"></router-view>
            </transition>
        </div>
        <div class="sec-footer">
            <Footer />
        </div>
    </div>
</template>
<script>
import { selectArray } from 'common/js/navigators'
import Footer from '_c/footer'
export default {
    data () {
        return {
            activeDate: 0,
            navTitle: ['景区介绍', '温泉朔源'],
            currentIndex: 0,
            cateid: 1
        }
    },
    mounted() {
        let { cateid, cateindex } = this.$route.query
        this.currentIndex = cateindex
        this.cateid = cateid
    },
    methods: {
        selectTag (item) {
            this.$router.push({
                name: item.displaytype,
                query: {
                    cateid: item.cateid,
                    cateindex: item.cateindex
                }
            })
        }
    },
    computed: {
        getSecondNav () {
            return selectArray[this.cateid] ? selectArray[this.cateid].children : []
        },
        firTitle () {
            return selectArray[this.cateid] ? selectArray[this.cateid].parent : '东泉美景'
        }
    },
    components: {
        Footer
    },
    watch: {
        '$route' (to, from) {
            this.cateid = to.query.cateid
            this.currentIndex = to.query.cateindex
            this.activeDate = new Date().getTime()
        }
    }
}
</script>
<style lang="less" scoped>
.viewpage{
    margin: 0;
    .sec-header{
        overflow: hidden;
    }
    .sec-nav{
        width: 1200px;
        margin: 0 auto;
        .cur-category{
            text-align: right;
            font-size: 25px;
            font-weight: 700;
            color: #e0c073;
            padding: 10px;
            span{
                font-weight: 100;
                font-size: 22px;
                margin-right: 10px;
            }
        }
        ul{
            border-bottom: 1px solid #ccc;
            .item{
                margin-right: 5px;
                line-height: 50px;
                padding: 0 42px;
                float: left;
                cursor: pointer;
                transition: all .5s;
                &:hover{
                    color: white;
                    background-color: #e0c073;
                }
            }
            .item-active{
                color: white;
                background-color: #e0c073;
            }
            &::after{
                content: '';
                display: block;
                clear: both;
            }
        }
    }
    .content-wrapper{
        width: 1200px;
        margin: 0 auto;
        min-height: 600px;
        overflow: hidden;
    }
    .sec-footer{
        background-color: #e0c073;
    }
}
@media screen and(max-width: 1200px) {
    .viewpage{
        .sec-nav{
            width: 100%;
        }
        .content-wrapper{
            width: 100%;
            & /deep/ img{
                width: 100%;
            }
        }
    }
}
@media screen and(max-width: 400px) {
    .viewpage{
        .sec-header{
            height: 140px;
            img{
                height: 100%;
            }
        }
    }
}
.secrouter-enter-active{
    transition: all .5s;
}
.secrouter-enter{
    opacity: 0;
    transform: translateX(100%);
}
.secrouter-enter-to{
    opacity: 1;
    transform: translateX(0);
}
</style>



